/* --- Admin: tabela wniosków --- */
.container {
  max-width: 960px;
  margin: 0 auto;
  padding: 24px 16px;
}

#pendingTable {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  background: #fff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  overflow: hidden;
}

#pendingTable thead th {
  text-align: left;
  font-weight: 600;
  padding: 12px 16px;
  background: #f8fafc;
  border-bottom: 1px solid #e5e7eb;
  font-size: 14px;
  letter-spacing: 0.2px;
}

#pendingTable tbody td {
  padding: 12px 16px;
  border-bottom: 1px solid #f1f5f9;
  vertical-align: middle;
  font-size: 14px;
}

#pendingTable tbody tr:last-child td {
  border-bottom: 0;
}

#pendingTable tbody tr:hover {
  background: #f9fafb;
}

/* Przyciski akcji */
#pendingTable button {
  appearance: none;
  border: 1px solid #d0d7de;
  background: #fff;
  color: #111827;
  border-radius: 10px;
  padding: 8px 12px;
  font-size: 14px;
  cursor: pointer;
  transition: background 0.15s ease, border-color 0.15s ease, transform 0.05s ease;
}

#pendingTable button:active {
  transform: translateY(1px);
}

#pendingTable .btn-approve {
  border-color: #2e7d32;
  color: #2e7d32;
  font-weight: 600;
}

#pendingTable .btn-approve:hover {
  background: #e8f5e9;
}

#pendingTable .btn-reject {
  border-color: #c62828;
  color: #c62828;
  font-weight: 600;
  margin-left: 8px;
}

#pendingTable .btn-reject:hover {
  background: #ffebee;
}

/* Status (gdyby był używany) */
#status {
  margin: 12px 0 16px;
  padding: 10px 12px;
  border-radius: 10px;
  background: #f8fafc;
  border: 1px solid #e5e7eb;
  font-size: 14px;
}

/* Responsywność — niewielkie ekrany: miękki scroll poziomy */
@media (max-width: 640px) {
  .container {
    padding: 16px 12px;
  }
  #pendingTable {
    display: block;
    overflow-x: auto;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
  }
  #pendingTable thead,
  #pendingTable tbody,
  #pendingTable tr {
    width: max-content;
  }
}
