.form input[type=text],
.form textarea {
  border: 1px solid #d0d7de;
  border-radius: 10px;
  background: #fff;
  padding: 10px 12px;
  font: inherit;
  color: #111;
  transition: border-color 0.15s, box-shadow 0.15s;
}

.form textarea {
  resize: vertical;
  min-height: 180px;
}

.form input[type=text]:focus,
.form textarea:focus {
  border-color: #1877F2;
  box-shadow: 0 0 0 3px rgba(24, 119, 242, 0.12);
  outline: none;
}

.form input[type=file] {
  border: 1px dashed #cbd5e1;
  border-radius: 10px;
  padding: 10px;
  background: #fafafa;
}

.form .image-preview,
.form .existing-images .existing-grid {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.form .ex-item {
  display: inline-flex;
  align-items: center;
  gap: 8px;
}

.form .form-error {
  color: #b91c1c;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 10px;
  padding: 10px 12px;
}

.form .actions {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
}

/* breakpoints */
@media (max-width: 640px) {
  .form .actions {
    justify-content: stretch;
  }
  .form .actions .btn, .form .actions .user-chip {
    flex: 1;
    justify-content: center;
  }
}
/* meta układ (po lewej info, po prawej przyciski) */
.article-meta {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}

.meta-left {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
  color: #666;
}

.meta-right {
  margin-left: auto;
  display: flex;
  gap: 8px;
  align-items: center;
}

/* przycisk "Lubię to" (bez ikony) */
.btn--like {
  padding: 4px 12px;
  border-radius: 999px;
  line-height: 1;
  border: 1px solid #e5e7eb;
  background: #fff;
  transition: background-color 0.15s, border-color 0.15s, color 0.15s, opacity 0.15s;
}

.btn--like:hover {
  background: #f8fafc;
  border-color: #cbd5e1;
}

.btn--like:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.btn--like:focus-visible {
  outline: 2px solid #2563eb;
  outline-offset: 2px;
}

.btn--like[aria-pressed=true] {
  background: #f0f6ff;
  border-color: #1877F2;
  color: #1877F2;
}

.page-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.page-title {
  margin: 0;
}

.page-header #newArticleTopBtn {
  margin-left: auto;
}

@media (max-width: 640px) {
  .page-header {
    flex-wrap: wrap;
  }
  .page-header #newArticleTopBtn {
    margin-left: 0;
  }
}
.btn--sm {
  padding: 4px 10px;
  font-size: 0.9rem;
  border-radius: 10px;
}

#backToListBtn {
  margin-bottom: 8px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
}

/* === Filtry listy artykułów === */
.filters {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 12px 0 16px;
}
.filters > *:first-child {
  flex: 1;
}

.filters input[type=search],
.filters select {
  width: 100%;
  border: 1px solid #d0d7de;
  background: #fff;
  color: #111;
  border-radius: 10px;
  padding: 10px 12px;
  font: inherit;
  transition: border-color 0.15s ease, box-shadow 0.15s ease, background-color 0.15s ease;
}

.filters input[type=search]::placeholder {
  color: #9aa0a6;
}

.filters input[type=search]:focus,
.filters select:focus {
  outline: none;
  border-color: #1877F2;
  box-shadow: 0 0 0 3px rgba(24, 119, 242, 0.12);
}

/* Select – ładna strzałka */
.filters select {
  max-width: 220px;
  appearance: none;
  -webkit-appearance: none;
  background-image: linear-gradient(45deg, transparent 50%, #6b7280 50%), linear-gradient(135deg, #6b7280 50%, transparent 50%);
  background-position: calc(100% - 18px) 56%, calc(100% - 12px) 56%;
  background-size: 6px 6px, 6px 6px;
  background-repeat: no-repeat;
  padding-right: 32px;
}

/* Hover – delikatny */
.filters input[type=search]:hover,
.filters select:hover {
  background-color: #fafbfc;
}

/* Mobile – układ w kolumnie */
@media (max-width: 640px) {
  .filters {
    flex-direction: column;
    align-items: stretch;
    gap: 10px;
  }
  .filters select {
    max-width: none;
  }
}
.btn--danger {
  color: #b91c1c;
  border: 1px solid #fecaca;
  background: #fff5f5;
  border-radius: 10px;
  padding: 6px 12px;
}

.btn--danger:hover {
  background: #fee2e2;
  border-color: #fca5a5;
}

.btn--danger:focus-visible {
  outline: 2px solid #ef4444;
  outline-offset: 2px;
}

.btn--danger:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.search-box {
  margin-bottom: 1em;
  display: flex;
  justify-content: flex-start;
}
.search-box input.input {
  padding: 0.5em 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  width: 100%;
  max-width: 300px;
}